<template>
  <!-- 部门列表 -->
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div>
      <mrc_ess_addBtn v-btnAuth="['add-department']"
                      @handleAdd='handleAdd'
                      :del='false'></mrc_ess_addBtn>
      <v-card v-if="$route.name =='department_list'">
        <v-card-text>
          <div class="title">
            <span class="departmentName">{{$t('label_Department_name')}}</span>
            <span class="staffNum">{{$t('label_Number_of_employees')}}</span>
          </div>
          <department-list v-for="item in list"
                           :key='item.uuid'
                           @delData='delData'
                           :level='0'
                           :obj='item'></department-list>
        </v-card-text>
      </v-card>
      <router-view v-else></router-view>
    </div>
  </mrc_ess_skLoaders>
</template>

<script>
import { getList } from '@/service/api/departmentManage'
import departmentList from '../components/deparmentList'
export default {
  components: { departmentList },
  props: {},
  data () {
    return {
      skLoading: true,
      org_level_code: '',
      list: []
    };
  },
  created () { },
  async mounted () {
    this.skLoading = true
    const uuid = this.$route.params.uuid || this.$store.state.uuid
    this.org_level_code = this.$route.params.code || this.$store.state.orgLevelCode
    // this.$route.meta.title = this.$route.params.name || this.$store.state.subPage_name
    this.$route.meta.title = '部门列表'
    await this.getData()
    this.skLoading = false
  },
  methods: {
    delData (uuid) {
      this.Recursion(this.companyList, uuid)
    },
    Recursion (arr, uuid) {
      if (arr.length <= 0) return ''
      arr.forEach((item, index) => {
        if (item.uuid == uuid) {
          arr.splice(index, 1)
        } else {
          if (item.children) {
            this.Recursion(item.children, uuid)
          }

        }
      })
    },
    async getData () {
      const list = await getList({
        org_level_code: this.org_level_code
      })
      if (list.code == 200) {
        this.list = list.data
      }
    },
    // 添加部门
    handleAdd () {
      this.$store.commit('set_uuid2', '')
      this.$router.push({
        name: 'department_add'
      })
    }
  },
  computed: {},
  watch: {
    async '$route' (val) {
      if (val.name == 'department_list') {
        await this.getData()
      }
    }
  },
};
</script>

<style scoped>
.title {
  display: flex;
  text-align: left;
}
.departmentName {
  font-size: 13px;
  flex: 1;
  margin-left: 5vw;
}
.staffNum {
  font-size: 13px;
  width: 17vw;
}
</style>
